import * as React from "react";

import BigNumber from "bignumber.js";

import { connect, ConnectedReturnType } from "react-redux"; // Custom typings
import { bindActionCreators, Dispatch } from "redux";

import { AtomicConnectionStatus, OrderSettlement, OrderStatus } from "@renex/renex";
import { clearPopup, SetPopupPayload } from "../../actions/popup/popupActions";
import { seenMigration } from "../../actions/trader/migrationActions";
import { ApplicationData } from "../../reducers/types";
import { TraderOrderRecord } from "../../reducers/typesSdk";
import { AtomicNotice } from "../AtomicNotice";
import { Loading } from "../Loading";
import { SubmittedTradesTable } from "../SubmittedTradesTable";
import { ViewBalancesTable } from "../ViewBalancesTable";

const orderFilter = (order: TraderOrderRecord): boolean =>
    order.computedOrderDetails.orderSettlement === OrderSettlement.RenEx &&
    order.status === OrderStatus.OPEN;

/**
 * MigrationPopup is a popup component for displaying terms and conditions
 */
class MigrationPopupClass extends React.Component<Props, State> {

    public constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            page: 0,
        };
    }

    /**
     * The main render function.
     * @dev Should have minimal computation, loops and anonymous functions.
     */
    public render(): React.ReactNode {
        const { page } = this.state;

        return <div className="popup migration">
            {this.pages()[page]()}
        </div>;
    }

    public renderWelcome = (): JSX.Element => {
        return <>
            <h1>Welcome</h1>
            <div className="migration--body">
                <div className="migration--description">
                    <p>
                        RenEx now uses <i>Swapperd</i> for trader balances and order execution.
                        Before continuing, please step through to see the changes.
                        </p>
                </div>
                <div className="migration--buttons">
                    <button onClick={this.next}>Next</button>
                </div>
            </div>
        </>;
    }

    // public renderWhatIsSwapperd = (): JSX.Element => {
    //     return <>
    //             <h1>What is Swapperd</h1>
    //             <div className="migration--body">
    //                 <div className="migration--description">
    //                     <p>
    //                         Swapperd is a
    //                     </p>
    //                 </div>
    //                 <div className="migration--buttons">
    //                     <button onClick={this.previous}>Previous</button>
    //                     <button onClick={this.next}>Next</button>
    //                 </div>
    //             </div>
    //         </>;
    // }

    public renderAtomicNotice = (): JSX.Element => {
        const { atomConnectionStatus } = this.props;

        const showLater =
            atomConnectionStatus !== AtomicConnectionStatus.ConnectedUnlocked;

        return <>
            <h1>Connect Swapperd</h1>
            <div className="migration--body">

                <div className="migration--description">
                    <p>
                        In order to use RenEx, you now use the Swapperd wallet. Connect it to trade:
                    </p>
                </div>
                <div className="migration--frame">
                    <AtomicNotice />
                </div>
                <div className="migration--buttons">
                    <button onClick={this.previous}>Previous</button>
                    {showLater ?
                        <button onClick={this.next}>Later</button> :
                        <button onClick={this.next} className="button--blue">Next</button>
                    }
                </div>
            </div>
        </>;
    }

    public renderCancelOrders = (): JSX.Element => {
        const { traderOrders } = this.props;

        const showLater = traderOrders.filter(orderFilter).size > 0;

        return <>
            <h1>Cancel old orders</h1>
            <div className="migration--body">

                <div className="migration--description">
                    <p>
                        RenEx now uses.
                        RenEx now uses a single Atomic swap system for all
                        trades and balances should now be stored in Swapperd to
                        perform trades.
                        Before withdrawing your legacy balances, please cancel
                        your old orders.
                    </p>
                </div>
                <div className="migration--frame">
                    <SubmittedTradesTable filter={orderFilter} />
                </div>
                <div className="migration--buttons">
                    <button onClick={this.previous}>Previous</button>
                    {showLater ?
                        <button onClick={this.next}>Later</button> :
                        <button onClick={this.next} className="button--blue">Next</button>
                    }
                </div>
            </div>
        </>;
    }

    public renderWithdraw = (): JSX.Element => {
        const { legacyBalances } = this.props;

        const showLater = (legacyBalances.filter(
            (balance: BigNumber | null) => (
                balance !== null &&
                !balance.isZero())).size > 0
        );

        return <>
            <h1>Withdraw legacy balances</h1>
            <div className="migration--body">

                <div className="migration--description">
                    <p>Balances are no longer stored in the RenEx balances contract.</p>
                </div>
                <div className="migration--frame">
                    <ViewBalancesTable />
                </div>
                <div className="migration--buttons">
                    <button onClick={this.previous}>Previous</button>
                    {showLater ?
                        <button onClick={this.next}>Later</button> :
                        <button onClick={this.next} className="button--blue">Next</button>
                    }
                </div>
            </div>
        </>;
    }

    public renderClose = (): JSX.Element => {
        return <>
            <div className="migration--body">
                <h1>Ready to go</h1>
                <div className="migration--description" />
                <div className="migration--buttons">
                    <button onClick={this.previous}>Previous</button>
                    <button onClick={this.done} className="button--blue">Close</button>
                </div>
            </div>
        </>;
    }

    public renderLoading = (): JSX.Element => {
        return <>
            <div className="migration--body">
                <Loading />
                <br />
                <div className="migration--buttons">
                    <button onClick={this.previous}>Previous</button>
                    <button onClick={this.next}>Next</button>
                </div>
            </div>
        </>;
    }

    /**
     * pages is an array of render functions to show for each step
     */
    private readonly pages = (): Array<() => JSX.Element> => {
        const { noLegacyBalances, noLegacyOrders } = this.props;

        const pageList = [];
        pageList.push(this.renderWelcome);
        // pageList.push(this.renderWhatIsSwapperd);
        pageList.push(this.renderAtomicNotice);

        if (noLegacyOrders === null) {
            pageList.push(this.renderLoading);
        } else if (noLegacyOrders === false) {
            pageList.push(this.renderCancelOrders);
        }

        if (noLegacyBalances === null) {
            pageList.push(this.renderLoading);
        } else if (noLegacyBalances === false) {
            pageList.push(this.renderWithdraw);
        }

        pageList.push(this.renderClose);

        return pageList;
    }

    private readonly next = (): void => {
        this.setState({ page: this.state.page + 1 });
    }

    private readonly previous = (): void => {
        this.setState({ page: this.state.page - 1 });
    }

    private readonly done = (): void => {
        const { address } = this.props;
        if (address) {
            this.props.actions.seenMigration({ address, seenMigration: true });
        }
        this.props.onDone();
    }
}

const mapStateToProps = (state: ApplicationData) => ({
    legacyBalances: state.trader.legacyBalances,
    traderOrders: state.traderBook.traderOrders,

    noLegacyOrders: state.trader.noLegacyOrders,
    noLegacyBalances: state.trader.noLegacyBalances,

    atomConnectionStatus: state.trader.atomConnectionStatus,

    address: state.trader.address,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
    actions: bindActionCreators({
        seenMigration,
        clearPopup,
    }, dispatch)
});

interface Props extends ReturnType<typeof mapStateToProps>, ConnectedReturnType<typeof mapDispatchToProps> {
    onDone(): void;
}

interface State {
    page: number;
}

export const MigrationPopup = connect(mapStateToProps, mapDispatchToProps)(MigrationPopupClass);

export const newMigrationPopup = (uuid: Symbol, onCancelAction: () => void, onDoneAction: () => void): SetPopupPayload => ({
    uuid,
    popup: <MigrationPopup onDone={onDoneAction} />,
    dismissible: true,
    onCancel: onCancelAction,
});
