import BigNumber from "bignumber.js";

import { BalanceAction, BalanceActionType, ComputedOrderDetails, MatchDetails, NumberInput, OrderInputs, OrderInputsAll, OrderSettlement, OrderSide, OrderStatus, OrderType, SwapOrder, TraderOrder, TransactionStatus, WBTCOrder } from "@renex/renex";
import BN from "bn.js";

import { Record } from "../lib/record";

class OrderInputsRecord extends Record({
    symbol: "",
    side: OrderSide.BUY,
    price: new BigNumber(0) as NumberInput,
    volume: new BigNumber(0) as NumberInput,
}) implements OrderInputs { }

class OrderInputsAllRecord extends Record({
    symbol: "",
    baseToken: "",
    quoteToken: "",
    side: OrderSide.BUY,
    price: new BigNumber(0),
    volume: new BigNumber(0),
    minVolume: new BigNumber(0),
    type: OrderType.LIMIT,
    orderSettlement: OrderSettlement.RenEx,
    nonce: new BN(0),
    expiry: 0,
}) implements OrderInputsAll { }

export class MatchDetailsRecord extends Record({
    orderID: "",
    matchedID: "",

    receivedVolume: new BigNumber(0),
    spentVolume: new BigNumber(0),
    fee: new BigNumber(0),
    receivedToken: "",
    spentToken: "",
}) implements MatchDetails { }

class ComputedOrderDetailsRecord extends Record({
    spendToken: "",
    receiveToken: "",
    receiveVolume: new BigNumber(0),
    spendVolume: new BigNumber(0),
    date: 0,
    feeAmount: new BigNumber(0),
    feeToken: "",
    orderSettlement: OrderSettlement.RenEx,
    nonce: new BN(0),
}) implements ComputedOrderDetails { }

const SwapOrderRecordDefault: SwapOrder = {
    swapServer: undefined,

    id: "",
    trader: "",
    status: OrderStatus.NOT_SUBMITTED,
    matchDetails: undefined as MatchDetailsRecord | undefined,

    computedOrderDetails: new ComputedOrderDetailsRecord(),
    orderInputs: new OrderInputsAllRecord(),
    transactionHash: "",
};
export class SwapOrderRecord extends Record(SwapOrderRecordDefault) implements SwapOrder { }
// TODO: Does Typescript have a way of getting this without explicitly defining it?
type SwapOrderRecordInner = typeof SwapOrderRecordDefault;

const WBTCOrderRecordDefault: WBTCOrder = {
    swapServer: true,

    id: "",
    trader: "",
    status: OrderStatus.NOT_SUBMITTED,
    matchDetails: undefined as MatchDetailsRecord | undefined,

    computedOrderDetails: new ComputedOrderDetailsRecord(),
    orderInputs: new OrderInputsRecord(),
};
export class WBTCOrderRecord extends Record(WBTCOrderRecordDefault) implements WBTCOrder { }
// TODO: Does Typescript have a way of getting this without explicitly defining it?
type WBTCOrderRecordInner = typeof WBTCOrderRecordDefault;

export class BalanceActionRecord extends Record({
    action: BalanceActionType.Withdraw,
    amount: new BigNumber(0),
    time: 0,
    status: TransactionStatus.Pending,
    token: "",
    trader: "",
    txHash: "",
    nonce: undefined as number | undefined,
}) implements BalanceAction { }

export type TraderOrderRecord = WBTCOrderRecord | SwapOrderRecord;

export const traderOrderToRecord = (traderOrder: TraderOrder): TraderOrderRecord => {
    if (traderOrder.swapServer) {
        const inner: WBTCOrderRecordInner = {
            swapServer: traderOrder.swapServer,
            id: traderOrder.id,
            trader: traderOrder.trader,
            status: traderOrder.status,
            matchDetails: traderOrder.matchDetails ? new MatchDetailsRecord(traderOrder.matchDetails) : undefined,

            computedOrderDetails: new ComputedOrderDetailsRecord(traderOrder.computedOrderDetails),
            orderInputs: new OrderInputsRecord(traderOrder.orderInputs),
        };
        return new WBTCOrderRecord(inner);
    } else {
        const inner: SwapOrderRecordInner = {
            swapServer: traderOrder.swapServer,
            id: traderOrder.id,
            trader: traderOrder.trader,
            status: traderOrder.status,
            matchDetails: traderOrder.matchDetails ? new MatchDetailsRecord(traderOrder.matchDetails) : undefined,

            computedOrderDetails: new ComputedOrderDetailsRecord(traderOrder.computedOrderDetails),
            orderInputs: new OrderInputsAllRecord(traderOrder.orderInputs),
            transactionHash: traderOrder.transactionHash,
        };
        return new SwapOrderRecord(inner);
    }
};
